import React from 'react';
import MyModal from './MyModal';

const Card = ({movies}) => {
    const [modalShow, setModalShow] = React.useState(false);
    return (
        <span>
          <span className='dib pa3 ma3'>
            <button className='tc dib br3 pa0 grow bw2 shadow-5' onClick={() => setModalShow(true)}>
              <img alt='user' src={movies.Poster} width='200' height='350' />
            </button>
                  <h2 className='white f4' style={{ width:"200px", whiteSpace: 'nowrap', overflow: 'hidden'}}>{movies.Title}</h2>
                  <h2 className='gray f5 tl' style={{ width:"200px", whiteSpace: 'nowrap', overflow: 'hidden'}}>{movies.Year}</h2>
          </span>
          <MyModal
            show={modalShow}
            onHide={() => setModalShow(false)}
            movie={movies}
          />
        </span>
    );
}

export default Card;